@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  color: #000000;
  line-height: 1;
  background-color: #ffffff;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  border: 0;
}

.page {
  height: 100%;
}

.page__body {
  min-width: 280px;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
@media (min-width: 1440px) {
  .page__body {
    grid-template-rows: 1fr min-content;
  }
}

.page__header {
  position: relative;
}
@media (min-width: 768px) {
  .page__header {
    margin-top: 25px;
  }
}
@media (min-width: 1440px) {
  .page__header {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    z-index: 1;
    width: 100%;
    margin-top: 55px;
  }
  .page__header--catalog, .page__header--form {
    position: static;
    left: 0;
    transform: none;
  }
}

.page__main {
  overflow: hidden;
}

.header__container {
  padding-top: 13px;
  padding-bottom: 14px;
  border-bottom: 1px solid #e6e6e6;
}
.header__container:has(.site-navigation--no-js) {
  padding-bottom: 0;
  border-bottom: none;
}
@media (min-width: 768px) {
  .header__container {
    display: flex;
    align-items: center;
    padding: 0;
    border-bottom: none;
    column-gap: 94px;
  }
}
@media (min-width: 1440px) {
  .header__container {
    width: 1220px;
    justify-content: space-between;
    padding-top: 0;
    padding-bottom: 0;
    align-items: start;
  }
}

.header__logo {
  width: 200px;
  flex-shrink: 0;
}
@media (min-width: 768px) {
  .header__logo {
    width: 174px;
  }
  .header__logo--form {
    width: 177px;
  }
}
@media (min-width: 1440px) {
  .header__logo {
    width: 202px;
  }
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  transition: opacity 0.3s ease;
}
.logo:hover {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}

.logo__img {
  margin-right: 56px;
  transition: opacity 0.3s ease;
}
@media (min-width: 768px) {
  .logo__img {
    width: 60px;
    height: 50px;
    margin-right: 13px;
  }
}
@media (min-width: 1440px) {
  .logo__img {
    margin-right: 14px;
    width: 70px;
    height: 59px;
  }
}

.logo__text {
  width: 101px;
  height: 18px;
  transition: opacity 0.3s ease;
}
@media (min-width: 1440px) {
  .logo__text {
    width: 118px;
    height: 21px;
  }
}

.site-navigation--no-js + .site-navigation__button {
  display: none;
}
.site-navigation--no-js .site-navigation__list {
  display: block;
  position: static;
  margin-left: -20px;
  margin-right: -20px;
  height: 200px;
  width: initial;
}
@media (min-width: 768px) {
  .site-navigation--no-js .site-navigation__list {
    display: flex;
    margin: 0;
    height: initial;
  }
}
.site-navigation--is-opened .site-navigation__list {
  height: 198px;
  z-index: 2;
}

.site-navigation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: absolute;
  z-index: 2;
  top: 66px;
  left: 0;
  background-color: #ffffff;
  width: 100%;
  overflow: hidden;
  height: 0;
  transition: height 0.6s ease;
}
@media (min-width: 768px) {
  .site-navigation__list {
    background-color: transparent;
    height: initial;
    position: static;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 33px;
  }
}
@media (min-width: 1440px) {
  .site-navigation__list {
    max-width: 500px;
    transform: translate(-2px, 4px);
  }
  .site-navigation__list--form {
    transform: translate(-3px, 4px);
  }
  .site-navigation__list--catalog {
    transform: translate(-4px, 4px);
  }
}

.site-navigation__item {
  display: flex;
  justify-content: center;
}

.site-navigation__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
  padding-top: 22px;
  padding-bottom: 24px;
  flex-grow: 1;
  text-align: center;
  border-bottom: 1px solid #e6e6e6;
  margin-left: -20px;
  margin-right: -20px;
  transition: opacity 0.3s ease;
}
.site-navigation__link:hover {
  opacity: 0.6;
}
.site-navigation__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .site-navigation__link {
    font-size: 18px;
    line-height: 1.33;
    padding-top: 14px;
    padding-bottom: 12px;
    margin-left: 0;
    margin-right: 0;
    border-bottom: none;
  }
}
@media (min-width: 1440px) {
  .site-navigation__link {
    font-size: 20px;
    color: #ffffff;
  }
}
.site-navigation__link--inner {
  color: #111111;
}
@media (min-width: 768px) {
  .site-navigation__link--current {
    text-decoration: underline;
    text-decoration-color: #68b738;
    text-decoration-thickness: 2px;
    text-underline-position: under;
    text-underline-offset: 8px;
  }
}
@media (min-width: 1440px) {
  .site-navigation__link--current {
    text-underline-offset: 8px;
    text-decoration-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .site-navigation__link--current-catalog {
    text-underline-offset: 6px;
  }
}
@media (min-width: 1440px) {
  .site-navigation__link--current-catalog {
    text-underline-offset: 8px;
  }
}
@media (min-width: 768px) {
  .site-navigation__link--current-form {
    text-underline-offset: 6px;
  }
}
@media (min-width: 1440px) {
  .site-navigation__link--current-form {
    text-underline-offset: 8px;
  }
}

.site-navigation__link--current.site-navigation__link--inner {
  text-decoration-color: #68b738;
}

.site-navigation__button-line {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transition: width 0.6s ease, transform 0.6s ease;
}
.site-navigation__button-line:nth-of-type(3) {
  margin-bottom: 4px;
}
.site-navigation__button-line:nth-of-type(2) {
  margin-bottom: 6px;
}
.site-navigation__button-line:nth-of-type(1) {
  margin-bottom: 6px;
}

.site-navigation__button {
  position: absolute;
  flex-direction: column;
  width: 24px;
  height: 24px;
  background-color: transparent;
  border: none;
  padding: 0;
  top: 22px;
  right: 22px;
  cursor: pointer;
}
.site-navigation__button:hover {
  opacity: 0.6;
}
.site-navigation__button:active {
  opacity: 0.3;
}
.site-navigation__button--form {
  top: 21px;
  right: 20px;
}
.site-navigation__button--catalog {
  top: 21px;
  right: 21px;
}
.site-navigation__button--is-pressed {
  right: 18px;
}
.site-navigation__button--is-pressed .site-navigation__button-line:nth-of-type(1) {
  transform: translateY(8px) rotate(45deg);
}
.site-navigation__button--is-pressed .site-navigation__button-line:nth-of-type(2) {
  width: 0;
}
.site-navigation__button--is-pressed .site-navigation__button-line:nth-of-type(3) {
  transform: translateY(-8px) rotate(-45deg);
}
@media (min-width: 768px) {
  .site-navigation__button {
    display: none;
  }
}

